body {
    font-size: 20px;
    background-image: url("bckg.png");
    background-repeat: no-repeat;
    background-size: cover;
}

#menu {
    height: 15vh;
}

#sec {
    display: none;
}

p:first-child {
    margin-top: 0;
    width: 60%;
}

.item {
    background-color: #2C3E50 !important;
    color: white !important;
    border-radius: 0px !important;
    margin-right: 100px !important;
}

#body {
    text-align: justify;
    font-weight: bold;
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: none !important;
    box-shadow: 0 1px 0 0 white;
    background-color: transparent;
    border: none;
}

.dropdown {
    border: 3px solid rgba(34, 36, 38, 0.15) !important;
}

#distance {
    background-color: white;
    border: 3px solid rgba(34, 36, 38, 0.15);
    width: 60%;
    height: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
}

#selecting {
    width: 60%;
    display: flex;
    justify-content: space-between;
}

.enabled {
    opacity: 1 !important;
}

.enabled:hover {
    background-color: #ABB2B9 !important;
}

#from {
    width: 45%;
}

#to {
    width: 45%;
}

#button {
    color: white;
    width: 60%;
    font-weight: bold;
    cursor: pointer;
    background-color: #2C3E50;
    opacity: 0.6;
}

@media (max-width: 800px) {
    #selecting {
        flex-direction: column;

        align-items: center;
    }

    #from {
        width: 100%;
        margin-bottom: 20px;
    }

    #to {
        width: 100%;
    }
}

.button {
    width: 60%;
    height: 40px;
}
